<template>
<div class="HelloIndexBox">
    <div></div>
    <div class = "CHJOOLogo">
        <div id = "REye">
            <div id="OrbitalCavityR">
                <div id="EyeballR">
                </div>
            </div>
        </div>
        <div id = "LEye">
            <div id="OrbitalCavityL">
                <div id="EyeballL">
                </div>
            </div>
        </div>
        <div class="Mouth">
        </div>
    </div>
</div>
</template>
<script setup>
import { ref, onMounted, onUnmounted, h } from 'vue';
var mouseX = ref(0);
var mouseY = ref(0);

function handleMouseMove(event) {
    // 浏览大小计算
    var width = ref(window.innerWidth)
    var height = ref(window.innerHeight)
    // 鼠标位置计算
    mouseX.value = event.clientX;
    mouseY.value = event.clientY;
    EyePositionCalculation(parseFloat(width.value), parseFloat(height.value))
}
// 眼球移动计算
function EyePositionCalculation(WindowsWidth, WindowsHeight){
    // 眼框计算
    var EyeballL = document.getElementById("EyeballL")
    var EyeballR = document.getElementById("EyeballR")
    var EyeStyle = window.getComputedStyle(EyeballR);
    var EyeWidth = parseFloat(EyeStyle.getPropertyValue('width'));
    var EyeHeight = parseFloat(EyeStyle.getPropertyValue('height'));

    var OrbitalCavityR = document.getElementById("OrbitalCavityR")
    var computedStyle = window.getComputedStyle(OrbitalCavityR);
    var rect = OrbitalCavityR.getBoundingClientRect();
    var ORwidth = parseFloat(computedStyle.getPropertyValue('width'));
    var ORheight = parseFloat(computedStyle.getPropertyValue('height'));
    var left = rect.left
    var top = rect.top
    // 空隙宽度
    var  WidthRatio = (1 - ((left+ORwidth/2)/WindowsWidth))*2
    var  HeightRatio = ((top+ORheight/2)/WindowsHeight)*2
    // 上下眼动
    var EyeballRTop = (mouseY.value * ORheight)/ WindowsHeight * HeightRatio
    if (EyeballRTop > ORheight-EyeHeight){
        EyeballRTop =  ORheight-EyeHeight
    }
    // 左眼配置
    var EyeWidthL = (mouseX.value * ORwidth)/ WindowsWidth * WidthRatio
    if (EyeWidthL > ORwidth-EyeWidth){
        EyeWidthL = ORwidth-EyeWidth
    }
    // 右眼配置
    var ExcessRightEye = mouseX.value > WindowsWidth * (1-WidthRatio) ? mouseX.value - WindowsWidth * (1-WidthRatio) : 0
    var EyeWidthR = ((ExcessRightEye * ORwidth)/ WindowsWidth * WidthRatio)
    if(EyeWidthR > ORwidth-EyeWidth){
        EyeWidthR = ORwidth-EyeWidth
    }
    EyeballR.style.top = EyeballRTop + "px"
    EyeballR.style.left = EyeWidthR + "px"
    EyeballL.style.top = EyeballRTop + "px"
    EyeballL.style.left = EyeWidthL + "px"
}

onMounted(() => {
  window.addEventListener('mousemove', handleMouseMove);
});

onUnmounted(() => {
  window.removeEventListener('mousemove', handleMouseMove);
});

</script>
<style scoped>
@import '@assets/css/HomePage.css';
</style>